.img-thumbnail {
  padding: 0.25rem;
  background-color: #fff;
  border: 1px solid #dee2e6;
  border-radius: 0.25rem;
  width: 100%;
  height: auto;
  max-height: 200px;
}

.video-fluid {
  max-width: 85%;
  height: auto;
}

.bordered {
  padding: 0.25rem;
  background-color: #fff;
  border: 1px solid #dee2e6;
  border-radius: 0.25rem;
}

pre code {
  font-size: 87.5%;
}

code, body {
  line-height: 1.0;
}

